import React, { Component } from "react";

class Task extends Component {
  render() {
    const { title, location, detail, date } = this.props;
    return (
      <div>
        <h4>{title}</h4>
        <ul>
          <li>Location: {location}</li>
          <li>Details: {detail}</li>
          <li>Date: {date}</li>
        </ul>
      </div>
    );
  }
}

export default Task;
